app-sidebar {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 2rem 0 2rem;
    max-width: 23rem;
    width: 100%;
    height: 100%;
    background-color: var(--blue-700);

    .sidebar {
        &-header {
            .logo {
                margin: 0 auto;
                max-width: 14rem;
                max-height: 4.8rem;
                width: 100%;
                height: 100%;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                }
            }
        }

        &-content {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            height: 100%;
        }

        &-nav {
            min-height: 21rem;
            max-height: 25rem;
        }

        &-footer {
            display: flex;
            justify-items: center;
            align-items: center;
            width: 100%;
            min-height: 6rem;
            overflow: hidden;

            // centered status Online, Offline
            .synchronization-status {
                .status-container {
                    .offline,
                    .online {
                        justify-content: center;
                    }
                }
            }
        }
    }
}

.cdk-drag-placeholder {
    opacity: 0.3;
}

.cdk-drag-animating {
    transition: transform 250ms cubic-bezier(0, 0, 0.2, 1);
}

.cdk-drag-preview {
    &.wallet {
        color: var(--white-500);
        background: var(--blue-500);

        &.price {
            color: var(--gray-800);

            .percent {
                color: var(--gray-800);
            }
        }
    }
}
