.assets-table {
    .token-logo {
        background: var(--blue-300);
        min-width: 5.5rem;
        min-height: 5.5rem;
        max-width: 5.5rem;
        max-height: 5.5rem;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        overflow: hidden;

        img {
            max-width: 3.2rem;
            max-height: 3.2rem;
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
}
